
.Comment .ChannelCard {
    margin: 0;
}

.Comment .ChannelCard__thumbnail {
    margin-right: 16px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: transparent;
    overflow: hidden;
}

.Comment .ChannelCard__link {
    margin-right: 8px;
    margin-bottom: 2px;
    font-size: .9rem;
    font-weight: 500;
    line-height: 1rem;
}

.Comment .ChannelCard__video-publish-date {
    margin-left: 5px;
}

.Comment .ChannelCard__details {
    margin-top: -18px;
}

.Comment__body {
    margin-left: 55px;
    margin-top: -25px;
}

.Comment__body p {
    font-size: .8rem;
    font-weight: 400;
    line-height: 1.5rem;
    margin-top: 2px;
}


